const localData = require('../../data/home_comic.js');
const util = require('../../utils/util.js');
const api = require('../../config/api.js'); //获取应用实例
//获取应用实例
const app = getApp();

export default {
    data() {
        return {
            banner: [],
            blockList: [],
            recommendEveryDay: {},
            updateTodayList: []
        };
    },

    // 页面分享
    onShareAppMessage: function() {
        let that = this;
        return {
            title: '仿腾讯动漫',
            desc: '唯爱与美食不可辜负',
            path: '/pages/comic/comic'
        };
    },
    onLoad: function() {
        this.getHomeComicData();
        uni.setNavigationBarTitle({
            title: '仿腾讯动漫'
        });
    },

    onPullDownRefresh() {
        uni.showLoading({
            title: '正在加载中'
        }); //在标题栏中显示加载

        setTimeout(() => {
            this.getHomeComicData();
            uni.hideLoading(); //完成停止加载

            uni.stopPullDownRefresh(); //停止下拉刷新
        }, 2000);
    },

    components: {},
    props: {},
    methods: {
        getHomeComicData: function() {
            // 无网络，为了展示，用本地数据
            
            this.setData({
                banner: localData.banner,
                blockList: localData.blockList,
                recommendEveryDay: localData.recommendEveryDay,
                updateTodayList: localData.updateTodayList
            }); // 网络请求
            // util.request(api.homeComic).then((res) => {
            //   this.setData({
            //     banner: res.banner,
            //     blockList: res.blockList,
            //     recommendEveryDay: res.recommendEveryDay,
            //     updateTodayList: res.updateTodayList,
            //   });
            // });
        },
        setData: function(obj) {
            let that = this;
            let keys = [];
            let val, data;
            Object.keys(obj).forEach(function(key) {
                keys = key.split('.');
                val = obj[key];
                data = that.$data;
                keys.forEach(function(key2, index) {
                    if (index + 1 == keys.length) {
                        that.$set(data, key2, val);
                    } else {
                        if (!data[key2]) {
                            that.$set(data, key2, {});
                        }
                    }

                    data = data[key2];
                });
            });
        }
    }
};
